.Story {
    height: 130vh;
    top: 0rem;
    position: relative;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-direction: column;
        flex-direction: column;
    margin-top: 4rem;
}

.StoryImg {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: end;
        align-items: flex-end;
    margin-top: -3rem;
    z-index: 1000;
    width: 64rem;
    height: 30rem;
    background-image: -webkit-gradient(linear, left top, left bottom, from(rgba(245, 246, 252, 0)), color-stop(rgba(255, 255, 255, 0.199)),to(rgb(255, 255, 255))),
    url(../../img/storyOne.jpg);
    background-image: -webkit-linear-gradient(top, rgba(245, 246, 252, 0), rgba(255, 255, 255, 0.199),rgb(255, 255, 255)),
    url(../../img/storyOne.jpg);
    background-image: -o-linear-gradient(top, rgba(245, 246, 252, 0), rgba(255, 255, 255, 0.199),rgb(255, 255, 255)),
    url(../../img/storyOne.jpg);
    background-image: linear-gradient(to bottom, rgba(245, 246, 252, 0), rgba(255, 255, 255, 0.199),rgb(255, 255, 255)),
    url(../../img/storyOne.jpg);
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    text-align: center;
    border-radius: 4px;
}

.HeaderStory h1 {
    font-family: 'Playfair Display', serif;
    font-size: 4em;
    color: rgba(135, 72, 69, 0.849);
    text-align: center;
    margin-top: -3rem;
}

.StoryBody {
    text-align: center;
    max-width: 60rem;
    font-family: 'Playfair Display', serif;
    line-height: 3;
    padding: 2rem;
    margin-top: -2rem;
}


@media only screen and (max-width: 700px) {
    .StoryBody {
        font-size: 0.9em;
    }

    .HeaderStory h1 {
        font-size: 3em;
    }

    .Story {
        height: 160vh;
        margin-top: 1rem;
    }
}

@media only screen and (max-width: 780px) {
    .Story {
        height: auto;
    }

}





