.Location {
    text-align: center;
    height: 95vh;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-align: center;
        align-items: center;
}

.Location h1 {
    font-family: 'Playfair Display', serif;
    font-size: 3em;
    margin-bottom: 5rem;
    margin-top: 1rem;
    color: rgba(135, 72, 69, 0.849);
}

.LocationsContainer {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-column-gap: 8rem;
}

.Box h2 {
    font-size: 1.7em;
    letter-spacing: 2px;
    font-family: 'Playfair Display', serif;
    color: rgba(135, 72, 69, 0.849);
    width: 100%;
}

.Box p {
    font-size: 0.8em;
    line-height: 2;
    color:rgba(135, 72, 69, 0.849);
}

.Box h5 {
    font-size: 1.7em;
    margin-top: -0.9rem;
    margin-bottom: -0.9rem;
    color: rgba(135, 72, 69, 0.849);
    font-weight: 100;

}

.LocationOneImg {
    background-image: -webkit-gradient(linear, left top, left bottom, from(rgba(245, 246, 252, 0)), color-stop(rgba(255, 255, 255, 0.199)),to(rgb(255, 255, 255))),
    url(../../img/locationOne.jpg);
    background-image: -webkit-linear-gradient(top, rgba(245, 246, 252, 0), rgba(255, 255, 255, 0.199),rgb(255, 255, 255)),
    url(../../img/locationOne.jpg);
    background-image: -o-linear-gradient(top, rgba(245, 246, 252, 0), rgba(255, 255, 255, 0.199),rgb(255, 255, 255)),
    url(../../img/locationOne.jpg);
    background-image: linear-gradient(to bottom, rgba(245, 246, 252, 0), rgba(255, 255, 255, 0.199),rgb(255, 255, 255)),
    url(../../img/locationOne.jpg);
    background-position: center;
    background-size: cover;
    height: 15rem;
    width: auto;
    border-radius: 3px;
    margin-bottom: -2.3rem;
}

.LocationTwoImg {
    background-image: -webkit-gradient(linear, left top, left bottom, from(rgba(245, 246, 252, 0)), color-stop(rgba(255, 255, 255, 0.199)),to(rgb(255, 255, 255))),
    url(../../img/locationTwo.jpg);
    background-image: -webkit-linear-gradient(top, rgba(245, 246, 252, 0), rgba(255, 255, 255, 0.199),rgb(255, 255, 255)),
    url(../../img/locationTwo.jpg);
    background-image: -o-linear-gradient(top, rgba(245, 246, 252, 0), rgba(255, 255, 255, 0.199),rgb(255, 255, 255)),
    url(../../img/locationTwo.jpg);
    background-image: linear-gradient(to bottom, rgba(245, 246, 252, 0), rgba(255, 255, 255, 0.199),rgb(255, 255, 255)),
    url(../../img/locationTwo.jpg);
    background-position: center;
    background-size: cover;
    height: 15rem;
    width: auto;
    border-radius: 2px;
    margin-bottom: -2.3rem;
}

.Box {
    max-width: 20rem;
    height: 25rem;
}

.BoxOne {
    grid-column: 1/2;
}

.BoxTwo {
    grid-column: 2/3;
}

.Address {
    font-style: italic;
    font-weight: 700;
}

@media only screen and (max-width: 900px) {
    .LocationsContainer {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        grid-column-gap: 3rem;
    }
}

@media only screen and (max-width: 800px) {
    .LocationsContainer {
        display: grid;
        grid-template-rows: repeat(2, 25rem);
        grid-template-columns: 100%;
        grid-row-gap: 4rem;
        -ms-flex-pack: center;
            justify-content: center;
        -ms-flex-align: center;
            align-items: center;
    }

    .BoxOne {
        grid-row: 1/2;
        grid-column: 1/2;
    }
    
    .BoxTwo {
        grid-row: 2/3;
        grid-column: 1/2;
    }

    .Location {
        height: 155vh;
    }

    .Location h1 {
        font-size: 2em;
        margin-bottom: 3rem;
        margin-top: 1rem;
    }
    
}

@media only screen and (max-width: 780px) {
    .Location {
        height: auto;
    }

    .LocationsContainer {
        margin-bottom: 7rem;
    }

}

@media only screen and (max-width: 480px) {
    .Location {
        margin-top: -3rem;
    }

}










